
.blog-slide-container{
    width: 60vw;
    max-height: 52vh;
    padding-top: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    overflow: auto;
    box-shadow: #000000 0px 15px 15px -6px inset,
    #000000 0px -15px 15px -6px inset;
}
.blog-slide-container::-webkit-scrollbar{
    display: none;
}
.blog-slide{
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 190px;
    width: 60%;
    margin: 0 15px;
    margin-bottom: 20px;
}
.blog-slide-blur{
    position: absolute;
    z-index: -1;
    width: 100%;
    height: 100%;
    filter: blur(2px);
    background: rgba(43, 43, 43, 0.4);
    border: 1px solid rgba(44, 44, 44, 0.7);
    border-radius: 8px;
    box-shadow: 4px 7px 15px 0px rgba(0, 0, 0, 0.4), 5px -5px 12px 0px rgba(54, 54, 54, 0.5) inset, -5px 5px 12px 0px rgba(54, 54, 54, 0.5) inset, -1px 0px 2px 0px rgba(54, 54, 54, 0.2) inset;
}

.blog-img-container{
    height: 120px;
    width:  120px;
    margin-left: 15px;
    z-index: -1;
}

.blog-img-container img{
    width: 100%;
    height: 100%;
    border-radius: 20px;
    object-fit: cover;
}

.blog-content{
    width: calc(100% - 160px);
    height: 140px;
    display:  flex;
    margin: 10px 0;
    margin-right: 15px;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-between;
    text-align: left;
}
.slide-title{
    font-size: 15px;
}
.slide-description{
    font-size: 9px;
}
.blog-content .slide-btn{
    width: 70px;
    height: 25px;
    font-size: 8px;
    background-color: unset;
    border: unset;
    color: white;
    border: solid white 1px;
    border-radius: 1.1rem;
    transition: all .2s;

}
.blog-content .slide-btn:hover{
    transition: all .2s;
    background-color: rgba(245, 245, 245, .3);
    transform: scale(1.1);
}


.blog-content *{
    margin: 0;
}
